<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process8"></a>

<p><br></p>
<p><span class="rvts17">系统教程</span></p>
<p class="rvps2"><span class="rvts33">1. </span><span class="rvts29">同伴</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber25.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">与大多数</span><span class="rvts20">RPG</span><span class="rvts21">游戏相似，《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》中的主角也是需要招募伙伴进行冒险的。你可以在初入欢乐堡后，招募</span><span class="rvts20">6</span><span class="rvts21">名源力者为你的同伴，他们分别是猩红王子，费恩，塞碧莉，洛许，伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹，野兽。</span>
<br><span class="rvts21">如果你在刚遇到他们的时候不小心杀掉了他们，那么他们将永久不可用。如果你解散了你的伙伴，还可以在他们初始出现的地方找到他们。</span>
<br><span class="rvts21">然而，一旦离开欢乐堡登上复仇女神号，在与达利斯的海上大战前，将会是最后一次选择同伴的机会。之后，未选择的源力者同伴会死掉，永远不可用。</span>
<br><span class="rvts21">不过，即便你一个同伴也没有招募，也可以在复仇女神号上找到一个女雇佣兵蜥蜴，出钱的话，她会给你配备你想要的同伴。</span>
<br><span class="rvts21">源力者同伴和花钱雇佣的同伴的最大区别在于，源力者有自己的支线任务，而普通被雇佣的同伴只是一个战斗伙伴而已。因此强烈建议招募</span><span class="rvts20">6</span><span class="rvts21">名源力者同伴，这样可以使冒险的旅途更加多样化，更加充满趣味。</span>
<br><span class="rvts23"></span>
<br><span class="rvts33">2. </span><span class="rvts29">任务</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber26.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">游戏中的一些</span><span class="rvts20">NPC</span><span class="rvts21">，物品，场景都可能会触发各种不同的任务。它们中有些比较容易完成，有些是穿插在一起的，略微有些复杂。有些支线任务一旦一些环节出现问题（重要</span><span class="rvts20">NPC</span><span class="rvts21">死了，重要物证毁了等）就永久无法关闭，换句话说，就是无法成功完成。</span>
<br><span class="rvts21">这些任务基本上都是一个个小的支线，当然，主线任务可以总结为每张地图只有一个或两个，该游戏的趣味在于拥有繁多的支线任务。</span>
<br><span class="rvts21">取得任务的进展或完成任务都会增加角色的经验值。经验值的积累会提升角色的等级，增强更多的属性。</span>
<br><span class="rvts23"></span>
<br><span class="rvts33">3. </span><span class="rvts29">装备和物品</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber27.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》的装备遵循阶梯式等级提升的算法。说白了，大多数情况下，装备的好坏往往取决于装备的等级，而并非装备的品质程度。</span>
<br><span class="rvts21">鉴于各种天赋和能力属性的提升算法都属于较小百分比的提升，而装备随着等级的提升比例百分比往往高于属性加成，因此强烈建议使用和角色同等级的装备，不要太在乎装备的品质程度。</span>
<br><span class="rvts21">即便是谈及装备的品质，也并非史诗装备绝对优于稀有装备或普通装备，它们只是拥有一些较强的属性而已。另外，一些史诗装备需要进行</span><span class="rvts20"></span><span class="rvts21">鉴定</span><span class="rvts20"></span><span class="rvts21">才能获得它们身上的属性信息，拥有较高</span><span class="rvts20"></span><span class="rvts21">博学</span><span class="rvts20"></span><span class="rvts21">的角色会优先鉴定物品。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber28.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">游戏中的物品栏可以通过索引功能进行效率的归类或查看。另外，不同的物品拥有不同的重量，背包的承载上限并不取决于背包容量的大小，而是取决于角色的力量值大小。</span>
<br><span class="rvts21">当超过了角色可以承载重量的</span><span class="rvts20">20%</span><span class="rvts21">，角色将无法移动。另外该游戏的一大</span><span class="rvts20">BUG</span><span class="rvts21">点是，无论同伴距离你多远，你都可以把包里的物品给他，或者他把他包里的物品给你。</span>
<br><span class="rvts23"></span>
<br><span class="rvts33">4. </span><span class="rvts29">战斗</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber29.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">游戏的战斗采取轮回制，遵循角色队伍的顺序优先级。一般情况下，首先进攻的角色是队伍中顺序主动性最强的。</span>
<br><span class="rvts21">该游戏的战斗拥有移动，攻击，防御，技能等要素。重要的技巧如下：</span>
<br><span class="rvts34">1. </span><span class="rvts35">使用普通的移动效果是最坏的移动方法。</span><span class="rvts21">利用飞翔，跳跃，传送可以更好的克服地形影响或地质影响，一方面减少移动所消耗的移动</span><span class="rvts20">AP</span><span class="rvts21">，另一方面可以避免特殊地形（举例，冰地质会使角色摔倒，错过一轮战斗）的潜在危害。</span>
<br><span class="rvts36">2. </span><span class="rvts37">普攻并非太差的攻击方法。</span><span class="rvts21">在很多时候，当我们的所有进攻技能都处于冷却状态时，往往我们需要进行普通攻击。但是千万不要以为普通攻击就不会打出伤害，盗贼的背刺，战士的平砍，以及魔杖和法杖的普功效果，在游戏前期是相当厉害的，后期则需要装备的支撑。</span>
<br><span class="rvts36">3. </span><span class="rvts37">元素亲和攻击和技能控制思想往往可以使战斗效果事倍功半</span><span class="rvts38">。</span><span class="rvts21">元素亲和指的是，你可以首先使用第一种元素进攻敌人，接着你可以使用第二种元素进攻敌人。两种元素可以产生更高的爆发或更好的控制效果。举例，你可以先让敌人中毒，之后使用火元素技能，将会引爆敌人身上的毒，造成爆炸效果，使敌人受到额外的伤害。技能控制指的是，你可以通过给敌人施加一种控制效果，在第二次进攻时使用相关技能致使敌人被控制或限制。举例，你可以使用具有冰冻效果的技能攻击敌人，接着，再次使用气元素技能，可以对它们产生眩晕效果。</span>
<br><span class="rvts36">4. </span><span class="rvts37">要更加注重物理和魔法的防御。</span><span class="rvts21">在该游戏中，物理护甲值和魔法护甲值是相当重要的两种护甲类型，当物理护甲值低于</span><span class="rvts20">0</span><span class="rvts21">时，角色将会受到嘲讽，流血，击倒，萎缩，残疾等效果；当魔法护甲值低于</span><span class="rvts20">0</span><span class="rvts21">时，角色将会受到沉睡，冰冻，魅惑，战栗等效果。因此，无论是物理护甲值还是魔法护甲值，都是比生命值更重要的防护要素。当然，这也是你可以用来打败敌人的战略措施。</span>
<br><span class="rvts34">5. </span><span class="rvts35">多调查敌人的元素抗性。</span><span class="rvts21">在进行战斗时，一些敌人会表现出较高的元素抵抗，比如说，不死族的毒元素抵抗是</span><span class="rvts20">100%</span><span class="rvts21">以上的，它们可以通过高于</span><span class="rvts20">100%</span><span class="rvts21">的元素抵抗恢复生命值，此时你就应该尽可能避免使用毒元素技能或武器攻击它们，避免产生反效果。</span>
<br><span class="rvts23"></span>
<br><span class="rvts33">5. </span><span class="rvts29">技能</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber30.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》的技能分为元素派系和武功派系。其中元素派系多为一些具备魔法伤害的技能，包含水，土，气，火元素派，这些元素的伤害加成往往取决于角色的智慧值。</span>
<br><span class="rvts21">武功派系多为一些具备物理攻击或特殊穿刺攻击的技能或辅助技能，包含战斗，猎人，恶棍，死灵，召唤，变形派系。这些派系的技能伤害加成一般取决于角色的主属性（力量，敏捷，智慧等），当然，变形和召唤派系的大多数技能取决于角色的等级。</span>
<br><span class="rvts21">角色可以使用在战斗中的技能种类数量取决于角色的记忆值大小。通常情况下，元素学派需求更高的记忆值；武功学派对记忆值的要求并不高。但如果是元素学派和武功学派混合双修的话，就有可能需要更多的记忆凹槽。</span>
<br><span class="rvts21">另外，一些物品可以提供给角色额外的技能使用。</span>
<br><span class="rvts23"></span>
<br><span class="rvts33">6. </span><span class="rvts29">合成</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber31.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">游戏中有一套强大的合成系统，与较早一代的原罪</span><span class="rvts20">1</span><span class="rvts21">不同，原罪</span><span class="rvts20">2</span><span class="rvts21">在合成方面并没有等级要求，并且一些配方和公式更加的灵活化，人性化。</span>
<br><span class="rvts21">合成物品可以使角色获得更加简洁，高效，省钱的效果（特别是弓箭，雷管的合成，卷轴的合成等</span><span class="rvts20">...</span><span class="rvts21">），另外有一些物品会是任务，剧情的需求品，因此尽可能的多寻找书籍了解更多的合成配方。</span>
<br><span class="rvts23"></span>
<br><span class="rvts33">7. </span><span class="rvts29">行为</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber32.jpg" display="block" max-width="100%"><span class="rvts18"></span>
<br><span class="rvts19">行为指的是在游戏中的各种行动，对话或表现。你可以选择和</span><span class="rvts20">NPC</span><span class="rvts21">角色对话的不同桥段，引出不同的后果。或者通过潜行和偷窃，用来盗取</span><span class="rvts20">NPC</span><span class="rvts21">身上的物品或金币。</span>
<br><span class="rvts21">这些都将影响</span><span class="rvts20">NPC</span><span class="rvts21">对你的态度或看法。重要的行为有以下几种：</span>
<br><span class="rvts34">1. </span><span class="rvts35">潜行。</span><span class="rvts21">决定你潜行时被敌人感知的强度，以及被抓的几率。</span>
<br><span class="rvts34">2. </span><span class="rvts35">说服。</span><span class="rvts21">决定你可以在于</span><span class="rvts20">NPC</span><span class="rvts21">的对话中，尝试说服他做某件事的成功几率。</span>
<br><span class="rvts34">3. </span><span class="rvts35">偷窃。</span><span class="rvts21">决定你可以盗取的物品价值和金币数量。</span>
<br><span class="rvts34">4. </span><span class="rvts35">讨价还价。</span><span class="rvts21">决定你和商人之间进行交易时的买卖价格优惠。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



